.review {
  padding: {
    top: 20px;
    left: 15px;
    right: 15px;
  }
  font-family: "IBM Plex Sans KR", sans-serif;
  transition: all 0.3s;
  max-width: inherit;
  min-width: 250px;
  cursor: pointer;
  &:nth-child(2n) {
    background-color: #2e2c2a;
  }

  .review__header {
    margin: {
      bottom: 10px;
    }
    font-weight: 600;

    .header__title {
      margin: {
        right: 10px;
      }
      font-size: 16px;
      line-height: 20px;
    }

    .header__rating {
      white-space: nowrap;
      line-height: 20px;
    }
  }

  .review__address {
    font-size: 13px;
    color: #ccc5b9;
  }

  .review__road-address {
    margin: {
      top: 3px;
      bottom: 5px;
    }
    font-size: 13px;
    color: #ccc5b9;
  }

  .review__memo {
    margin: {
      top: 35px;
      bottom: 5px;
    }
    padding: {
      left: 10px;
      right: 10px;
    }
    font-size: 14px;
    line-height: 20px;
  }

  .review__photo {
    padding: {
      left: 10px;
      right: 10px;
    }
    margin: {
      bottom: 25px;
    }
    width: 100%;
  }
  .review__footer {
    padding: {
      bottom: 20px;
    }

    .footer__date,
    .footer__author {
      margin: {
        right: 10px;
      }
      font-size: 12px;
      color: #ccc5b9;
    }
  }
  &.selected {
    background-color: #fffcf2;

    .review__header {
      .header__title {
        color: #252422;
      }
    }

    .review__address,
    .review__road-address,
    .review__memo {
      color: #403d39;
    }

    .review__footer {
      .footer__date,
      .footer__author {
        color: #403d39;
      }
    }
  }
}
